import React from "react";
import Delete from "./images/delete.png";
import Setting from "./images/settings.png";
import NewTeam from "./images/NewTeam.png";
import { useHistory } from "react-router-dom";
import { useMutation, useQuery } from "react-query";
import { axios, getToken } from "./utils/api";
import { Team } from "./utils/types";
import DataTable from "react-data-table-component";

export default function Teams() {
  const history = useHistory();
  const query = useQuery("teams", async () => {
    const data = await axios.post(
      "/api/teams",
      {},
      {
        headers: { authorization: "Bearer " + getToken() },
      }
    );
    return data.data as Team[];
  });
  const mutation = useMutation(
    (teamId: string) => {
      return axios.post(
        "/api/teams/delete",
        {
          teamId,
        },
        {
          headers: { authorization: "Bearer " + getToken() },
        }
      );
    },
    {
      onSuccess() {
        query.refetch();
      },
    }
  );
  return (
    <div style={{ padding: "2em 1em" }}>
      <div
        style={{ backgroundColor: "white", borderRadius: 10, padding: "2em" }}
      >
        <div>TEAMS</div>
        <div style={{ marginTop: "1em" }}>
          <DataTable
            customStyles={{
              headRow: {
                style: {
                  background:
                    "linear-gradient(90deg, #813866 0%, #ff9d82 100%)",
                  borderRadius: 8,
                  padding: "0 8px",
                },
              },
              headCells: {
                style: {
                  color: "white",
                },
              },
              rows: {
                style: {
                  background: "rgb(239, 243, 244)",
                  borderRadius: 8,
                  margin: "8px 0",
                  padding: "0 8px",
                },
              },
            }}
            columns={[
              {
                name: "Name",
                selector: "name",
              },
              {
                name: "Email",
                selector: "numUsers",
                cell: (row) => {
                  return (
                    <div
                      style={{
                        padding: "0 0.5em",
                        backgroundColor: "#B54679",
                        borderRadius: 4,
                        color: "white",
                        fontSize: "1.5em",
                        textAlign: "center",
                      }}
                    >
                      {row.numUsers}
                    </div>
                  );
                },
              },
              {
                name: "Actions",
                cell: (row) => {
                  return (
                    <div style={{ padding: "0.5em 0" }}>
                      <img
                        src={Setting}
                        alt="Setting"
                        style={{
                          objectFit: "contain",
                          maxHeight: 25,
                          borderRadius: "50%",
                        }}
                      />
                      <img
                        src={Delete}
                        alt="Delete"
                        onClick={() => {
                          const doDelete = window.confirm(
                            `Do you want to delete ${row.name} team?`
                          );
                          if (doDelete) {
                            mutation.mutate(row.id);
                          }
                        }}
                        style={{
                          objectFit: "contain",
                          maxHeight: 25,
                          borderRadius: "50%",
                        }}
                      />
                    </div>
                  );
                },
              },
            ]}
            data={query.data ?? []}
          />
        </div>
      </div>
      <div
        style={{ width: "100%", textAlign: "right" }}
        onClick={() => {
          history.push("/teams/new");
        }}
      >
        <img
          src={NewTeam}
          alt="New Team"
          style={{ maxHeight: 50, marginRight: 50 }}
        />
      </div>
    </div>
  );
}
